<template>
	<div class="manage tc">
		<button class="btn btn-success btnadd" v-on:click="add">新增</button>
		<div class="input-area" v-show="showAdd">
			<input type="text" placeholder="请输入人员姓名" v-model="nameValue">
			<button v-on:click="addName">确定</button>
		</div>
		<table>
			<tr>
				<th>姓名</th>
				<th>操作</th>
			</tr>
			<tr v-for="(item,index) in peoples">
				<td>{{item.name}}</td>
				<td :id='index'><span v-on:click="edit">编辑</span><span v-on:click="del">删除</span></td>
			</tr>
		</table>
		<div class="wrap" v-show="showEdit">
			<input type="text" placeholder="请输入姓名" v-model="newName">
			<button  v-on:click='cancel'>取消</button>	
			<button  v-on:click='editName'>确定</button>	
		</div>
		<footer-nav></footer-nav>
	</div>
</template>
<style scoped>
	.btnadd{
		width: 10rem;
	}
	.input-area{margin-top: 1rem;}
	table{
		width: 80%;
    	margin: 2rem auto;
	}
	table tr td span{
		padding: 0 0.5rem;
	}
</style>
<script>
	import FooterNav from '../../components/footer.vue'
	export default{
		components:{
			FooterNav
		},
		data(){
			return {
				isNowPage:true,
				showAdd:false,
				showEdit:false,
				peoples:[{'name':'Jack'},{'name':'Joe'}],
				nameValue:'',
				newName:'',
				editId:''
			}
		},
		
		methods:{
			add(){
				this.showAdd=true
			},
			addName(){
				var v=this.nameValue;
				if(v.trim()==''){
					alert("请输入新增人员姓名")
				}else{
					var data={}
					data.name=v
					this.peoples.push(data)
				}
			},
			del(e){
				var id=e.target.offsetParent.id
				this.peoples.splice(id,1)
			},
			edit(e){
				this.showEdit=true;
				var id=e.target.offsetParent.id;
				this.editId=id;
				this.newName=this.peoples[id].name

			},
			cancel(){
				this.showEdit=false
			},
			editName(){
				var v=this.newName
				if(v.trim()==''){
					alert("请输入姓名")
				}else{
					this.peoples[this.editId].name=this.newName
					this.showEdit=false

				}
				
			}
		}
	}
</script>


